.upload-box{
    outline: 2px dashed #92b0b2;
    background-color: #c8dadf;
    font-size: 1.25rem;
    position: relative;
    padding: 20px 20px;
    outline-offset: -10px;
    text-align: center;
}
.upload-box input[type=file]{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    display: inline;
    line-height: 60px;
}
.upload-box input[type=file] + label{
    display: inline;
    white-space: nowrap;
    cursor: pointer;
    text-overflow: ellipsis;
    overflow: hidden;
}
.upload-box input[type=file] + label:hover strong{
    color: #39bfd3;
}

.upload-box.has-advanced-upload
{
    outline: 2px dashed #92b0b3;
    outline-offset: -10px;
    -webkit-transition: outline-offset .15s ease-in-out, background-color .15s linear;
    transition: outline-offset .15s ease-in-out, background-color .15s linear;
}

.upload-box.is-dragover{
    outline-offset: -20px;
    outline-color: #c8dadf;
    background-color: #fff;
}